import React from "react";
import { Device, Authentication, Fleet } from "@formant/data-sdk";

const context = React.createContext<Device | null>(null);
const useDevice = () => React.useContext(context);

const DeviceProvider = ({ children }: { children: React.ReactNode }) => {
  const [device, setDevice] = React.useState<Device | null>(null);

  React.useEffect(() => {
    const fetchDevice = async () => {
      await Authentication.waitTilAuthenticated();
      const device = await Fleet.getCurrentDevice();
      setDevice(device);
    };

    fetchDevice();
  }, []);

  if (!device) return <>Waiting for device...</>;

  return <context.Provider value={device}>{children}</context.Provider>;
};

export { DeviceProvider, useDevice };
