import React from "react";
import ReactDOM from "react-dom/client";

import App from "./App";
import { DeviceProvider } from "./DeviceWrapper";
import { ModuleDataProvider } from "./ModuleWrapper";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <DeviceProvider>
      <ModuleDataProvider>
        <App />
      </ModuleDataProvider>
    </DeviceProvider>
  </React.StrictMode>
);
