import { App, Fleet, IEvent } from "@formant/data-sdk";
import "@formant/ui-sdk-realtime-player";
import React from "react";

import "./App.css";
import { useDevice } from "./DeviceWrapper";
import { useModuleData } from "./ModuleWrapper";

const tsToIsoFormat = (ts: number): string => {
  const date = new Date(ts);
  return date.toISOString();
};

const isotimeToTs = (isotime: string): number => new Date(isotime).getTime();

const timeformat = (time: string | number) => {
  const d = new Date(time);
  return d.toLocaleTimeString("en-GB");
};

const isTsInsideEvent = (ts: Number, event: IEvent) => {
  return (
    ts >= isotimeToTs(event.time) &&
    (!event.endTime || ts <= isotimeToTs(event.endTime))
  );
};

const goTo = (ts: number | string) => {
  App.goToTime(new Date(ts));
};

const Module = () => {
  const moduleData = useModuleData();
  const device = useDevice();
  const [events, setEvents] = React.useState<IEvent[]>([]);

  React.useEffect(() => {
    const fetchEvents = async () => {
      if (!device || !moduleData) return;

      const events = await Fleet.queryEvents({
        deviceIds: [device.id],
        eventTypes: ["annotation"],
        start: tsToIsoFormat(moduleData.queryRange.start - 60_000),
        end: tsToIsoFormat(moduleData.queryRange.end + 60_000),
      });
      // console.log(events[0]);
      setEvents(events);
    };

    fetchEvents();
  }, [device, moduleData]);

  if (!moduleData) return null;

  return (
    <>
      <h1>
        Attempts from {timeformat(moduleData.queryRange.start - 60_000)} to{" "}
        {timeformat(moduleData.queryRange.end + 60_000)}
      </h1>
      <table>
        <thead>
          <tr>
            <td></td>
            <td>Id</td>
            <td>Start</td>
            <td>End</td>
            <td>Run Id</td>
          </tr>
        </thead>
        <tbody>
          {events.map((event) => (
            <tr
              className={
                isTsInsideEvent(moduleData.time, event) ? "current" : ""
              }
              key={event.id}
            >
              <td>{isTsInsideEvent(moduleData.time, event) && ">"}</td>
              <td>
                <a onClick={() => goTo(event.time)}>{event.message}</a>
              </td>
              <td>{timeformat(event.time)}</td>
              <td>{event.endTime && timeformat(event.endTime)}</td>
              <td>{event.tags?.run_id}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
};

export default Module;
